'use client'
import { useState, useEffect } from 'react'
import { addPost } from './addPost';
import { formatArticle } from "../utils.js"
export default function Home() {
  const [id, setId] = useState();
  const [title, setTitle] = useState();
  const [imgCaption, setImgCaption] = useState();
  const [article, setArticle] = useState();
  function handleArticleChange(input) {
    setArticle(formatArticle(input));
  }
  return (
    <div>
      <form action={() => addPost(title, id, imgCaption, article)}>
        <input value={id} onChange={e => setId(e.target.value)} />
        <input value={title} onChange={e => setTitle(e.target.value)} />
        <input value={imgCaption} onChange={e => setImgCaption(e.target.value)} />
        <textarea onChange={e => handleArticleChange(e.target.value)} />
      </form>
    </div>
  );
}
